import { _decorator, Component, EventTouch, Node, ScrollView, Vec2 } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('scrollTest')
export class scrollTest extends Component {
    start() {
        this.node.on(Node.EventType.TOUCH_START, (event) => {
            
        });
    
        this.node.on(Node.EventType.TOUCH_END, (event: EventTouch) => {
    
          const startPos = event.touch.getStartLocation();
          const prePos = event.touch.getPreviousLocation();
    
    
          let offset;
          if (startPos.x > prePos.x) {
            const scroll = this.node.getComponent(ScrollView);
            console.log('========',scroll.getScrollOffset().x)
            scroll.scrollToOffset(
              new Vec2(
                scroll.getScrollOffset().x + 500,
                0
              ),
              1,
              true
            );
          } else {
            const scroll = this.node.getComponent(ScrollView);
            console.log('========',scroll.getScrollOffset().x)
            scroll.scrollToOffset(
              new Vec2(
                scroll.getScrollOffset().x - 500,
                0
              ),
              1,
              true
            );
          }
          

    
        });
    }

    update(deltaTime: number) {
        
    }
}

